import React from "react";
import one from "./images/1.jpg";
import two from "./images/2.jpg";
import three from "./images/3.jpg";
import four from "./images/4.jpg";
import five from "./images/5.jpg";
import ViewPicture from "./ViewPicture.js";
import M from "materialize-css";
import { DEFAULT_ALBUM } from './App.js';

const DEFAULT_LENGTH = 5;

let carouselOptions = {
  duration: 200,
  dist: -50,
  numVisible: DEFAULT_LENGTH,
};

let modalOptions = {
  opacity: 0,
};

const captions = [
  "Victorian Belle",
  "Peacock Lane",
  "Zoolights",
  "Fire on the Mountain",
  "Steven's House",
];

const date = ["2018", "2018", "2018", "2019", "2020"];

const pushDefault = (arr, picture, alt, caption, date) => {
  arr.push({
    picture: picture,
    alt: alt,
    caption: caption,
    date: date,
  });
}

let defaultArr = [];
pushDefault(defaultArr, five, five, captions[4], date[4]);
pushDefault(defaultArr, one, one, captions[0], date[0]);
pushDefault(defaultArr, two, two, captions[1], date[1]);
pushDefault(defaultArr, three, three, captions[2], date[2]);
pushDefault(defaultArr, four, four, captions[3], date[3]);

let currIndex = 0;

class PhotoCarousel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      picture: one,
      caption: "",
      date: "",
      modal: null,
	  defPhotos: defaultArr
    };

	this.viewRef = React.createRef();
  }

  componentDidUpdate(prevProps) {
    if (this.props !== prevProps && this.props.photos.length !== 0) {
      let elem = document.querySelector(".carousel");
	  const numPhotos = this.props.photos.length;
      carouselOptions.numVisible = (numPhotos % 2 === 0) ? numPhotos - 1 : numPhotos;
      M.Carousel.init(elem, carouselOptions);
    }

    if (this.props.photos.length === 0) {
      let elem = document.querySelector(".carousel");
      carouselOptions.numVisible = DEFAULT_LENGTH;
      M.Carousel.init(elem, carouselOptions);
    }
  }

  componentDidMount() {
    let elem = document.querySelector(".carousel");
    let carousel = M.Carousel.init(elem, carouselOptions);

	modalOptions.onCloseStart = () => {
	  if (this.viewRef.current.state.editPhotoModal !== null) {
	    this.viewRef.current.state.editPhotoModal.close();
	  }

	  document.body.style.position = "initial";
	  document.documentElement.style.position = "initial";
	  document.documentElement.style.overflow = "initial";
	};

    let elem2 = document.getElementById("modal1");
    let modal = M.Modal.init(elem2, modalOptions);
    this.setState({ modal: modal, carousel: carousel});
  }

  handleClick = async (t) => {
    const pic = t.target.attributes.alt.value;
    const caption = t.target.attributes[0].value;
    const date = t.target.attributes[1].value;

	await this.viewRef.current.setState({
	  picture: pic,
	  date: date,
	  caption: caption
	});

	currIndex = this.findPhoto(pic);

	document.body.style.position = "relative";
	document.documentElement.style.position = "relative";
	document.documentElement.style.overflow = "hidden";

    this.state.modal.open();
  }

  deleteCallBack = () => {
	this.state.modal.close();
	this.props.setAlbum(this.props.album);
  }

  handleOnEdit = async(date, caption) => {
	this.viewRef.current.setState({
	  date: date,
	  caption: caption
	});

	this.props.setAlbumCallBack(this.props.album);
  };

  handleNavigate = async(e) => {
	let data = null;

	if (this.props.album === DEFAULT_ALBUM) {
	  data = defaultArr;
	} else {
	  data = this.props.photos;
	}

	if (e.key === "ArrowRight") {
      currIndex = (currIndex + 1) % data.length;

      const pic = data[currIndex].picture || data[currIndex].link;
      const caption = data[currIndex].caption;
      const date = data[currIndex].date;


	  await this.viewRef.current.setState({
	    picture: pic,
	    date: date,
	    caption: caption
	  });
	} else if (e.key === "ArrowLeft") {
	  
      currIndex = (currIndex !== 0) ? (currIndex - 1) % data.length : data.length - 1;

      const pic = data[currIndex].picture || data[currIndex].link;
      const caption = data[currIndex].caption;
      const date = data[currIndex].date;

	  await this.viewRef.current.setState({
	    picture: pic,
	    date: date,
	    caption: caption
	  });
	}
  };

  findPhoto = photo => {
	let photos = null;

	if (this.props.album === DEFAULT_ALBUM) {
	  photos = defaultArr;
	} else {
	  photos = this.props.photos;
	}
	
	for (let i = 0; i < photos.length; i += 1) {
	  if (photo === (photos[i].picture || photos[i].link)) {
	    return i;
	  }
	}

	return -1;
  };

  render() {
    return (
      <div style={{ height: "100%" }} onKeyDown={this.handleNavigate}>
        <ViewPicture
          current={this.state.picture}
          caption={this.state.caption}
          date={this.state.date}
		  album={this.props.album}
		  deleteCallBack={this.deleteCallBack}
		  token={this.props.idtoken}
		  ref={this.viewRef}
		  handleOnEdit={this.handleOnEdit}
        />
		<div className="row">
		<div className="col s12">
        <h2 style={{marginTop: "5%", marginBottom: "5%" }} >
		  {
		    (this.props.album === DEFAULT_ALBUM || this.props.album === undefined) ? "Album Preview" : this.props.album + " Album"
		  }
		</h2>
		</div>
		<div className="col s12">
        <div className="carousel" style={{ height: "600px" }}>
          {this.props.photos.map((item) => (
            <a className="carousel-item" href="#!">
              <img
                data-caption={item.caption}
                data-date={item.date}
                alt={item.link}
                src={item.link}
                onClick={this.handleClick}
              />
            </a>
          ))}
          {(this.props.album === DEFAULT_ALBUM) ? defaultArr.map((item) => (
            <a className="carousel-item" href="#!" key={item.picture}>
              <img
                data-caption={item.caption}
                data-date={item.date}
                alt={item.alt}
                src={item.picture}
                onClick={this.handleClick}
              />
            </a>
          )) : null}
		</div>
        </div>
		</div>
      </div>
    );
  }
}

export default PhotoCarousel;

