import React from "react";
import M from "materialize-css";
import { Button } from "react-materialize";
import { Editor } from "@tinymce/tinymce-react";
import food from "./images/food.jpg";
import EditRecipePhoto from "./EditRecipePhoto.js";
import BeatLoader from "react-spinners/BeatLoader";
import axios from "axios";

const headersConfig = {
  "Content-Type": "multipart/form-data",
};

class EditRecipe extends React.Component {
  constructor(props) {
    super(props);
    this.editorRef = React.createRef();
	this.newNameRef = React.createRef();
	this.labelRef = React.createRef();

	this.state = {
	  file: null,
	  modal: null,
	  loading: false,
	  message: ""
	};
  }

  componentDidMount() {
	let elem = document.getElementById("edit-recipe");
	elem.tabIndex = "";
	elem.scrollTop = 0;
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
	if (this.state.file !== null && prevState.file !== null) {
	  this.setState({
		file: null
	  });
	}

	if (this.props.recipeName !== prevProps.recipeName) {
	  this.newNameRef.current.value = this.props.recipeName;
	}
  }

  handleSubmit = async () => {
	/*
    const request = {
      idtoken: this.props.idtoken,
      recipeName: this.props.recipeName,
      data: this.editorRef.current.editor.getContent(),
	  newName: this.newNameRef.current.value
    };
	*/

	// TODO: Need to validate if recipe name is not "" in input

	let formData = new FormData();
	formData.append("recipeName", this.props.recipeName);
	formData.append("data", this.editorRef.current.editor.getContent());
	formData.append("image", this.state.file);
	formData.append("idtoken", this.props.idtoken);
	formData.append("newName", this.newNameRef.current.value);

	if (this.isValidUrl(this.state.file)) {
	  formData.append("preview", this.state.file);
	}

	await this.setState({
	  loading: true
	});

    await axios.post("/updateRecipe", formData, { headers: headersConfig } ).then(
      async (response) => {
		this.setState({
		  loading: false,
		});

        await this.props.recipeEditCallback();
      },
      (error) => {
        console.log("Unsuccessful update");

		this.setState({
		  loading: false,
		  message: "Unable to update the recipe."
		});
      }
    );
  };

  handleAddPreview = () => {
	const elem = document.getElementById("edit-recipe-modal");
    const modal = M.Modal.init(elem);

	this.setState({
	  modal: modal
	});

	modal.open();
  };

  closeModal = () => {
	this.state.modal.close();
  };

  populateFile = data => {
	this.setState({
 	  file: data
	});
  };

  isValidUrl = string => {
    try {
      new URL(string);
    } catch (_) {
      return false;
    }

    return true;
  }

  render() {
    const data = this.props.recipeToEdit;

    return (
      <div id="edit-recipe" className="modal arg">
        <div className="modal-content">
		  <EditRecipePhoto populateFile={this.populateFile} closeModal={this.closeModal}/>
          <h4>Edit Recipe</h4>
			  <div className="row">
				<div className="row">
			      <a href="#!" className="waves-effect waves-light btn-large" onClick={this.handleAddPreview}><i className="material-icons left">photo</i>Add Preview Photo</a>
				</div>
				<div className="row" style={{width: "10%", height: "10%"}}>
				{ this.state.file === null ? <p>Default Photo</p> : null }
				{ this.state.file !== null && this.state.file !== "" ?
				  <img style={{width: "100%"}} alt="chosen" src={this.isValidUrl(this.state.file) ? this.state.file : URL.createObjectURL(this.state.file)}/> :
				  <img alt="default" src={food} style={{width: "100%"}} />
				}
				</div>
			  </div>
          <Editor
            key={data}
            ref={this.editorRef}
            value={data}
            apiKey="p234ptga2jzgn5mia4hnruilh2zzuhfchjd7ja98rwu5p6g4"
            init={{
              contextmenu: false,
              browser_spellcheck: true,
              height: 500,
              selector: "textarea",
              plugins: [
                "advlist autolink lists link image",
                "charmap print preview anchor help",
                "searchreplace visualblocks code",
                "insertdatetime media table paste wordcount",
              ],
              toolbar:
                "undo redo | fontselect | fontsize | forecolor | backcolor | formatselect | bold italic | alignleft aligncenter alignright | bullist numlist outdent indent | help",
              paste_enable_default_filters: false,
              paste_word_valid_elements: "b,strong,i,em,h1,h2,h3,h4,h5",
            }}
          />
          <br />
		    <div className="row">
              <div className="col s12">
			    Enter Recipe Name: 
                <div className="input-field inline">
                  <input ref={this.newNameRef} id="newRecipe" type="text" placeholder="Recipe Name"/>
                </div>
                </div>
              </div>

          <Button waves="light" onClick={this.handleSubmit}>
            Submit Changes
          </Button>
			      <div className="sweet-loading">
        <BeatLoader
          size={10}
          color={"#123abc"}
          loading={this.state.loading}
        />
		  <div className="row">
            <span className="helper-text">{this.state.message}</span>
		  </div>
      </div>
        </div>
        <div className="modal-footer">
          <a
            href="#!"
            className="modal-close waves-effect waves-green btn-flat"
          >
            Exit
          </a>
        </div>
      </div>
    );
  }
}

export default EditRecipe;

