import React from "react";

const footerStyle = {
  bottom: 0,
  position: "sticky",
};

class EditRecipePhoto extends React.Component {
  constructor(props) {
    super(props);

    this.uploadRef = React.createRef();
    this.dataRef = React.createRef();
    this.pasteRef = React.createRef();
  }

  handleUpload = (e) => {
    // Reset if using upload option
    this.pasteRef.current.value = "";
  };

  handlePaste = (e) => {
    // Reset if using URL option
    this.uploadRef.current.value = "";
  };

  handleSubmit = () => {
    if (this.pasteRef.current.value !== "") {
      this.props.populateFile(this.pasteRef.current.value);
    } else if (this.uploadRef.current.value !== "") {
      this.props.populateFile(this.dataRef.current.files[0]);
    } else {
      this.props.populateFile("");
    }

    this.props.closeModal();
  };

  handleClose = () => {
    this.props.closeModal();
  };

  render() {
    return (
      <div id="edit-recipe-modal" className="modal arg">
        <div className="modal-content">
          <h4>Add Preview Photo For Recipe</h4>
          <div className="row">
            <div className="col s6">
              <form action="#">
                <div className="file-field input-field">
                  <div className="btn">
                    <span>Upload File</span>
                    <input
                      onChange={this.handleUpload}
                      ref={this.dataRef}
                      type="file"
                      accept="image/*"
                    />
                  </div>
                  <div className="file-path-wrapper">
                    <input
                      ref={this.uploadRef}
                      className="file-path"
                      type="text"
                      accept="image/*"
                    />
                  </div>
                </div>
              </form>
              <div className="row">
                <div className="col s12">
                  Paste an image URL here:
                  <div className="input-field inline">
                    <input
                      ref={this.pasteRef}
                      onChange={this.handlePaste}
                      id="url_edit_inline"
                      type="text"
                    />
                    <label htmlFor="url_edit_inline">URL</label>
                  </div>
                </div>
              </div>
            </div>

          <div className="col s6">
            <p>
              Either upload a photo or add a URL to reference the desired recipe
              preview photo.
            </p>
            <p>
              Only one option can be selected, so attempting to use both options
              will clear the last option used.
            </p>
            <div className="row">
              <button
                className="btn waves-effect waves-light"
                type="submit"
                name="action"
                onClick={this.handleSubmit}
              >
                Submit
                <i className="material-icons right">send</i>
              </button>
            </div>
          </div>

          </div>
        </div>

        <div className="modal-footer" style={footerStyle}>
          <a
            href="#!"
            onClick={this.handleClose}
            className="waves-effect waves-green btn"
          >
            Exit
          </a>
        </div>
      </div>
    );
  }
}

export default EditRecipePhoto;

